'use client';

import { ReactNode, useEffect } from 'react';

export default function LayoutCorrectWidth({ children }: { children: ReactNode }) {
    useEffect(() => {
        const updateViewportUnits = () => {
            const vw = document.documentElement.clientWidth * 0.01;
            // const vh = document.documentElement.clientHeight * 0.01;
            // document.documentElement.style.setProperty('--vw', `${vw}px`);
            // document.documentElement.style.setProperty('--vh', `${vh}px`);
        };

        updateViewportUnits();
        window.addEventListener('resize', updateViewportUnits);

        return () => {
            window.removeEventListener('resize', updateViewportUnits);
        };
    }, []);

    return (
        <div
            className="w-fullh-full"
            style={{
                // width: 'calc(var(--vw, 1vw) * 100)',
                // height: 'calc(var(--vh, 1vh) * 100)'
            }}
        >
            {children}
        </div>
    );
}
