'use client'
import { RoutesHeader } from "@/lib/routesInfo"
import Image from "next/image";
import Link from "next/link"
import { usePathname } from "next/navigation"
import ZenitIzhevskLogo from "@/public/images/Zenit-Izhevs-Logo.png"
import { useEffect, useState } from "react";


export default function Header() {

    const PathName = usePathname();

    const [scrolled, setScrolled] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 10) {
                setScrolled(true); 
            } else {
                setScrolled(false);
            }
            console.log(window.scrollY)
        };

        window.addEventListener("scroll", handleScroll);

        return () => {
            window.removeEventListener("scroll", handleScroll);
        };
    }, []);

    return (
        <header className="h-[60px] w-full bg-[#001947] z-2000 flex justify-center sticky top-0">
            <div className="w-full max-w-[1200px] flex items-center justify-center px-4">
                <div
                    className="flex flex-1 justify-end"
                >
                    <Link
                        href="/"
                        className={`mr-[50px] shrink-0 z-1000 transition-all duration-300 linear ${scrolled ? 'scale-50' : 'scale-100'}`}
                    >
                        <Image
                            alt="Зенит Ижевск"
                            src={ZenitIzhevskLogo}
                            width={76}
                            height={104}
                        />
                    </Link>
                </div>

                {/* Навигация по центру (или чуть правее, как нужно) */}
                <nav className="flex gap-[60px] items-center">
                    {RoutesHeader.filter(route => route.path !== "/").map((route) => {
                        const isActive = PathName === route.path;

                        return (
                            <Link
                                key={route.title}
                                href={route.path}
                                className={`text-[20px] font-light hover:text-[#10839D] ${isActive ? "text-[#10839D] border-b border-[#10839D]" : ""}`}
                            >
                                {route.title.toUpperCase()}
                            </Link>
                        );
                    })}
                </nav>
                <div
                    className="flex-1"
                >
                </div>
            </div>
        </header>

    )
}